import { Elysia, t } from "elysia";
import { auth } from "@clerk/nextjs/server";
import { db } from "@/lib/db";
import { playerApplications, users, leaderboardEntries } from "@/lib/db/schema";
import { eq, desc } from "drizzle-orm";
import mcutil from "minecraft-server-util";

/**
 * Elysia API Server
 * 
 * All API routes are defined here using Elysia's clean syntax.
 * Routes are automatically available at:
 * - GET  /api/server-status
 * - GET  /api/leaderboard
 * - POST /api/apply
 */

// You may want to move these to environment variables in production
const MC_HOST = process.env.MC_SERVER_HOST || "aevummc.devflare.de";
const MC_PORT = process.env.MC_SERVER_PORT ? parseInt(process.env.MC_SERVER_PORT) : 25565;

const app = new Elysia({ prefix: "/api" })
  .onError(({ code, error, set }) => {
    console.error(`[${code}]`, error);

    if (code === "VALIDATION") {
      set.status = 400;
      return { error: "Validation failed", details: error.message };
    }

    set.status = 500;
    return { error: "Internal server error" };
  })
  /**
   * GET /api/server-status
   * Returns the current Minecraft server status
   */
  .get("/server-status", async () => {
    try {
      // Using minecraft-server-util to ping the Minecraft server
      const result = await mcutil.status(MC_HOST, MC_PORT, { timeout: 3000 });

      const status = {
        online: true,
        players: {
          online: result.players.online,
          max: result.players.max,
          sample: result.players.sample || [],
        },
        version: result.version.name,
        description: typeof result.motd === "object" ? result.motd.clean : result.motd,
        protocol: result.version.protocol,
        // Some queries may not provide all info
        favicon: result.favicon || null,
        // uptime, tps, ping may not be available from this endpoint,
        // so they are left as null or omitted
        uptime: null,
        tps: null,
        ping: null,
      };

      return status;
    } catch (error: any) {
      // If fetch fails, report offline status
      return {
        online: false,
        error: error.message || "Failed to connect to server"
      };
    }
  })
  /**
   * GET /api/leaderboard
   * Returns leaderboard entries sorted by type (kills, wealth, kdr, playtime)
   * Query params:
   *   - type: "kills" | "wealth" | "kdr" | "playtime" (default: "kills")
   *   - limit: number of entries to return (default: 10)
   */
  .get(
    "/leaderboard",
    async ({ query }) => {
      try {
        const type = (query.type as string) || "kills";
        const limit = parseInt((query.limit as string) || "10");

        // biome-ignore lint/suspicious/noImplicitAnyLet: <explanation>
        let orderBy;
        switch (type) {
          case "kills":
            orderBy = desc(leaderboardEntries.kills);
            break;
          case "wealth":
            orderBy = desc(leaderboardEntries.wealth);
            break;
          case "kdr":
            orderBy = desc(leaderboardEntries.kdr);
            break;
          case "playtime":
            orderBy = desc(leaderboardEntries.playtime);
            break;
          default:
            orderBy = desc(leaderboardEntries.kills);
        }

        const entries = await db
          .select()
          .from(leaderboardEntries)
          .orderBy(orderBy)
          .limit(limit);

        return entries;
      } catch (error) {
        console.error("Leaderboard error:", error);
        throw new Error("Failed to fetch leaderboard");
      }
    },
    {
      query: t.Object({
        type: t.Optional(t.String()),
        limit: t.Optional(t.String()),
      }),
    }
  )
  /**
   * POST /api/apply
   * Submit a player application (requires authentication)
   * Body:
   *   - minecraftUsername: string (required)
   *   - age: number | string (optional)
   *   - experience: string (optional)
   *   - whyJoin: string (required)
   */
  .post(
    "/apply",
    async ({ body, set }) => {
      try {
        // Get Clerk user ID from Next.js auth context
        const { userId } = await auth();

        if (!userId) {
          set.status = 401;
          return { error: "Unauthorized" };
        }

        const { minecraftUsername, age, experience, whyJoin } = body;

        // Validate input
        if (!minecraftUsername || !whyJoin) {
          set.status = 400;
          return { error: "Missing required fields" };
        }

        // Get or create user
        let user = await db
          .select()
          .from(users)
          .where(eq(users.clerkId, userId))
          .limit(1);

        if (user.length === 0) {
          // Create user record
          const [newUser] = await db
            .insert(users)
            .values({
              clerkId: userId,
              minecraftUsername,
              email: "", // Get from Clerk if needed
            })
            .returning();
          user = [newUser];
        }

        // Check for existing application
        const existing = await db
          .select()
          .from(playerApplications)
          .where(eq(playerApplications.userId, user[0].id))
          .limit(1);

        if (existing.length > 0) {
          set.status = 400;
          return { error: "Application already submitted" };
        }

        // Create application
        const [application] = await db
          .insert(playerApplications)
          .values({
            userId: user[0].id,
            minecraftUsername,
            age: age ? parseInt(String(age)) : null,
            experience,
            whyJoin,
            status: "pending",
          })
          .returning();

        return { success: true, application };
      } catch (error) {
        console.error("Application error:", error);
        set.status = 500;
        return { error: "Failed to submit application" };
      }
    },
    {
      body: t.Object({
        minecraftUsername: t.String(),
        age: t.Optional(t.Union([t.String(), t.Number()])),
        experience: t.Optional(t.String()),
        whyJoin: t.String(),
      }),
    }
  );

// Export the app instance for Eden
export { app };

// Export the app type for Eden
export type App = typeof app;

// Export HTTP method handlers for Next.js
export const GET = app.fetch;
export const POST = app.fetch;
export const PUT = app.fetch;
export const DELETE = app.fetch;
export const PATCH = app.fetch;
